import { ABI_PRESALE } from "../contracts/fairLaunch";
import { ABI_WETH } from "../contracts/weth";
import Web3 from "web3";
import { WETH_ADDRESS, PRESALE_ADDRESS } from "../../config/index";

export const getContribution = async (address) => {
  // Create a Web3 instance
  const web3 = new Web3(window.ethereum);

  // Create an instance of the contract using the ABI
  const contract = new web3.eth.Contract(ABI_PRESALE, PRESALE_ADDRESS);

  // Call the contribution function using the contract instance
  const contribution = await contract.methods.amountBought(address).call();

  // Return the contribution
  return contribution;
};


export const getUserTokenAmount = async (address) => {
    // Create a Web3 instance
    const web3 = new Web3(window.ethereum);
  
    // Create an instance of the contract using the ABI
    const contract = new web3.eth.Contract(ABI_PRESALE, PRESALE_ADDRESS);
  
    // Call the userTokenAmount function using the contract instance
    const tokenAmount = await contract.methods.userTokenAmount(address).call();
  
    // Return the token amount
    return tokenAmount;
  };



// Buys from the presale contract
export const buyFromPresale = async (amount) => {
  // Create a Web3 instance
  const web3 = new Web3(window.ethereum);

  // Get the current account
  const accounts = await web3.eth.getAccounts();
  const account = accounts[0];

  // Create an instance of the contract using the ABI
  const contract = new web3.eth.Contract(ABI_PRESALE, PRESALE_ADDRESS);

  // Call the Buy function using the contract instance with Ether value
  const tx = await contract.methods.Buy().send({
    from: account,
    value: amount // add the amount of Ether to send with the transaction
  });
}

export const getUserEtherBalance = async () => {
    // Create a Web3 instance
    const web3 = new Web3(window.ethereum);

    // Get the current account
    const accounts = await web3.eth.getAccounts();
    const account = accounts[0];
  
    // Get the Ether balance of the address
    const balanceWei = await web3.eth.getBalance(account);
    const etherBalance = web3.utils.fromWei(balanceWei, 'ether');
  
    // Return the Ether balance
    return parseFloat(etherBalance);
  };
  
// Returns the balance of WETH in the contract
export const getWETHcontractBalance = async () => {
  // Create a Web3 instance
  const web3 = new Web3(window.ethereum);

  // Get contract
  const account = PRESALE_ADDRESS;

  // Create an instance of the contract using the ABI
  const contract = new web3.eth.Contract(ABI_WETH, WETH_ADDRESS);

  // Call the balanceOf function using the contract instance
  const balance = await contract.methods.balanceOf(account).call();

  // Return the balance
  return balance;
};


// gets the account that is connected to the presale dapp

export const getConnectedAccount = async () => {
  // Create a Web3 instance
  const web3 = new Web3(window.ethereum);

  // Get the current account
  const accounts = await web3.eth.getAccounts();
  const account = accounts[0];

  return account;
};

