import React, { useState, useEffect } from "react";
import Web3 from 'web3';
import Box from "@mui/material/Box";
import IconButton from "@mui/material/IconButton";
import Input from "@mui/material/Input";
import FilledInput from "@mui/material/FilledInput";
import OutlinedInput from "@mui/material/OutlinedInput";
import InputLabel from "@mui/material/InputLabel";
import InputAdornment from "@mui/material/InputAdornment";
import FormHelperText from "@mui/material/FormHelperText";
import FormControl from "@mui/material/FormControl";
import TextField from "@mui/material/TextField";
import Visibility from "@mui/icons-material/Visibility";
import VisibilityOff from "@mui/icons-material/VisibilityOff";
import { Button, MenuItem, Select } from "@mui/material";
import { useDispatch, useSelector } from "react-redux";
import {
    getTokenAddress,
    getUsdtAddress,
    getRate,
    getMinAmount,
    exchangeToken,
    checkReferralToken,
    claimable,
    claimableOld,
} from "../utility/contractMethods/ico";
import {
    getTokenSymbol,
    tokenBalanceOfAddress,
    tokenAllowance,
    tokenApprove,
} from "../utility/contractMethods/token";

import {
    getContribution,
    buyFromPresale,
    getWETHcontractBalance,
    getUserTokenAmount,
    getConnectedAccount,
    getUserEtherBalance,
} from "../utility/contractMethods/fairLaunch";

import { showToastMessage } from "../redux/actions/toastNotification/index";
import { IcoAddress, date } from "../config/index";

const BuyBox = () => {
    const dispatch = useDispatch();
    const { selectedAddress, latestBlockNumberState, provider, wrongNetwork } =
        useSelector((state) => state.accounts);
    const [selectedCoin, setSelectedCoin] = React.useState("USDT");
    const handleChange = (event) => {
        setSelectedCoin(event.target.value);
    };

    const [tokenAddress, setTokenAddress] = useState(null);
    const [tokenName, setTokenName] = useState(null);
    const [tokenBalance, setTokenBalance] = useState(0);
    const [usdtAddress, setUsdtAddress] = useState(null);
    const [usdtName, setUsdtName] = useState(null);
    const [usdtBalance, setUsdtBalance] = useState(0);
    const [climableAmount, setClimable] = useState(0);
    const [rate, setRate] = useState(1);
    const [loading, setLoading] = useState(false);
    const [changeState, setChangeState] = useState(false);
    const [alreadyReferral, setAlreadyReferral] = useState(true);

    const [referralAddress, setReferralAddress] = useState("");
    const [amount, setAmount] = useState(0);
    const [toAmount, setToAmount] = useState(0);
    const [minAmount, setMinAmount] = useState(0);
    const [contractBalance, setContractBalance] = useState('');
    const [contribution, setContribution] = useState('');
    const [amounOfTokens, setAmounOfTokens] = useState('');

    useEffect(() => {
        if (selectedAddress) {
            const getUsdtAddressData = async () => {
                const usdtAddressData = await getUsdtAddress(selectedAddress);
                setUsdtAddress(usdtAddressData);
            };
            getUsdtAddressData();
        }
    }, [selectedAddress, provider, changeState]);


    useEffect(() => {
        // Use a setInterval function to call setApproved every 1 second
        const interval = setInterval(() => {
          async function fetchData() {
              
              const account = await getConnectedAccount();

              // Get the balance of WETH in the connected account's wallet
              const contractBalance = await getWETHcontractBalance();
              const ETHcontractBalance = Web3.utils.fromWei(contractBalance);
              setContractBalance(parseFloat(ETHcontractBalance).toLocaleString('en-US', { minimumFractionDigits: 0 }));

              // Get the contribution of the connected account
              const contribution = await getContribution(account);
              const ETHcontribution = Web3.utils.fromWei(contribution);
              setContribution(parseFloat(ETHcontribution).toLocaleString('en-US', { minimumFractionDigits: 0 }));        
             

              const amountOfTokens = await getUserTokenAmount(account);
              const amountOfTokensETH = Web3.utils.fromWei(amountOfTokens);
              console.log("amountOfTokensETH", amountOfTokensETH)
              setAmounOfTokens(parseFloat(amountOfTokensETH).toLocaleString('en-US', { minimumFractionDigits: 0 }));        
             
              const userBal = await getUserEtherBalance()
              console.log("userBal ", userBal)

              setUsdtBalance(userBal)
              
          }
          fetchData();
        }, 1000);  // 1000 milliseconds = 1 second
      
        // Clear the interval when the component unmounts
        return () => clearInterval(interval);
      }, []);  // Only run the effect once

      


    // useEffect(() => {
    //     if (selectedAddress && usdtAddress) {
    //         const getUsdtBalanceData = async () => {
    //             const usdtBalanceData = await tokenBalanceOfAddress(
    //                 usdtAddress,
    //                 selectedAddress,
    //             );

    //             setUsdtBalance(usdtBalanceData / 10 ** 18);
    //         };
    //         getUsdtBalanceData();
    //     }
    // }, [selectedAddress, usdtAddress, changeState]);

    useEffect(() => {
        if (selectedAddress) {
            const getRateData = async () => {
                const rateData = await getRate(selectedAddress);

                setRate(rateData / 100);
            };
            getRateData();
        }
    }, [selectedAddress, provider, changeState]);


    const setAmountData = async (e) => {
        // if(e.target.value < 250){
        //  showToastMessage("You must have to buy minimum $250 of CPI", "error");
        //  return
        // }
        setAmount(e.target.value);
        setToAmount(e.target.value * rate);
    };

    const handleBuyClick = async () => {
        // Perform buy logic here...
        buyFromPresale(amount * 1e18)
          .then(() => {
            console.log(amount);
            // Clear the input field after the transaction is confirmed
            setTimeout(() => {
              setAmount(0);
            }, 3000); // 3000 milliseconds = 3 seconds
          })
          .catch((error) => {
            console.log('Transaction failed:', error);
          });
      };

    const swapTokenData = async () => {
        try {
            setLoading(true);
            if (amount < minAmount) {
                dispatch(
                    showToastMessage(
                        `You must have to buy minimum amount of $${minAmount} USDT.`,
                        "error",
                    ),
                );
                setLoading(false);
            } else {
                const calAllowToken = await exchangeToken(
                    selectedAddress,
                    amount,
                    referralAddress ? referralAddress : IcoAddress,
                );
                if (calAllowToken.status === true) {
                    dispatch(
                        showToastMessage("Buy Token Successful", "success"),
                    );
                    setLoading(false);
                    setReferralAddress("");
                    setAmount(0);

                    setChangeState(!changeState);
                } else {
                    dispatch(
                        showToastMessage(calAllowToken.error.message, "error"),
                    );
                    setLoading(false);
                }
            }
        } catch (error) {
            dispatch(showToastMessage(error.message, "error"));
            setLoading(false);
        }
    };

    return (
        <Box className="tab-box" p={4}>
            <p className="tab-head" style={{ display: "inherit" }}>
                Buy CPI
                <Box style={{ float: "right" }}>
                    <p
                        className="adorment-disconnect"
                        style={{ color: "black" }}
                    >
                        {"PULSE"}&nbsp;
                    </p>
                    <p
                        className="adorment-disconnect"
                        style={{ color: "black" }}
                    >
                        {usdtBalance}
                    </p>
                </Box>
            </p>
            {/* <Box>
          <p className="adorment-disconnect" style={{ color: "black" }}>
            {"USDT BEP20"}&nbsp;
          </p>
          <p className="adorment-disconnect" style={{ color: "black" }}>
            {usdtBalance}
          </p>
        </Box> */}
            <FormControl
                fullWidth
                sx={{ m: 1, marginBottom: "20px" }}
                variant="standard"
            >
                {/* <InputLabel htmlFor="standard-adornment-amount">
            USDT BEP20
          </InputLabel> */}
                <Input
                    id="standard-adornment-amount"
                    value={selectedAddress}
                    startAdornment={
                        <InputAdornment position="start">
                            {" "}
                            <img src="images/Arrow - Down 4.png" />
                        </InputAdornment>
                    }
                    endAdornment={
                        <>
                            <InputAdornment position="end">
                                {" "}
                                {/* <p className="adorment-disconnect" style={{ color: "black" }}>
                    {usdtBalance}
                  </p> */}
                            </InputAdornment>
                        </>
                    }
                />
            </FormControl>
            {/* <Box sx={{ display: "flex" }}>
          <Box> */}
            <FormControl variant="standard" fullWidth>
                <InputLabel htmlFor="standard-adornment-amount">
                    Enter Amount
                </InputLabel>
                <Input
                    id="standard-adornment-amount"
                    value={amount}
                    type="number"
                    onChange={(e) => {
                        setAmountData(e);
                    }}
                    sx={{ m: 1, marginBottom: "20px" }}
                />
            </FormControl>
            {/* </Box> */}
            {/* <Box>
            <FormControl
              fullWidth
              //   sx={{ m: 1, marginBottom: "20px" }}
              variant="standard"
            >
              <InputLabel htmlFor="standard-adornment-p">Claimable</InputLabel>
              <Input
                id="standard-adornment-p"
                value={Number(climable) !== NaN ? climable : 0}
                disabled={true}
                type="number"
              />
            </FormControl>
          </Box>
        </Box> */}
            <FormControl
                fullWidth
                //   sx={{ m: 1, marginBottom: "20px" }}
                variant="standard"
            >
                <InputLabel htmlFor="standard-adornment-p">
                    Estimated Claimable {amounOfTokens} CPI
                    <br/>
                    Contributed {contribution} PULSE
                    <br/>
                    Contract Balance {contractBalance} WPULSE
                </InputLabel>

           
  
            </FormControl>
            <FormControl
                fullWidth
                sx={{ marginBottom: "20px" }}
                variant="standard"
            >

            </FormControl>
            <FormControl
                fullWidth
                sx={{ marginBottom: "20px" }}
                variant="standard"
            >
            </FormControl>

                <Button style={{marginTop:70}} className="buy-button" onClick={handleBuyClick}>
                    Buy Now
                </Button>
 
            {/* <b style={{ fontSize: "12px", marginTop: "5px" }}>
                Notes : You must have ‘USDT BEP20’ and ‘Smart Chain BNB’ tokens
                in your wallet before proceeding to Buy CPI token.
            </b> */}
        </Box>
    );
};
export default BuyBox;
